<script lang="coffeescript">

  import { createEventDispatcher, onMount, afterUpdate } from "svelte"

  import SearchInput from '../Form/SearchInput/SearchInput.svelte'
  import SortMenu from '../Overlay/Menu/SortMenu.svelte'
  import FilterMenu from '../Overlay/Menu/FilterMenu.svelte'
  import TopSplit from '../Layout/TopSplit.svelte'


  dispatch = createEventDispatcher()

  export list = []
  export id = uuid()

  export reverse = false
  export sortable = true # set this to false
  export filterable = true

  export searchValue = ""

  export sortKey = undefined

  export filterFn = (listToFilter, searchString)->
    return listToFilter

  export sortFn = (a, b)->
    return a > b

  # array.sortBy attributeName, reverseBoolean


  export sortOptions = [
    {
      ordinal: 1
      id: "name"
      label: "Name"
      ascending: true
      selected: true

    }
    {
      ordinal: 2
      id: "description"
      label: "Description"
      ascending: true
      selected: false

    }
    {
      ordinal: 3
      label: "Something Else"
      ascending: true
      selected: false
    }
  ]

  export filterOptions = [
    {
      ordinal: 1
      id: "name"
      label: "type 1"
      selected: false

    }
    {
      ordinal: 2
      id: "description"
      label: "type 2"
      selected: true

    }
    {
      ordinal: 3
      label: "type 3"
      selected: false
    }
  ]


  sortMenuButtonRef = undefined
  sortMenuRef = undefined

  showSortMenu = false
  showFilterMenu = false

  sortMenu = ()->
    showSortMenu = true

  filterMenuButtonRef = undefined

  filterMenu = ()->
    showFilterMenu = true

  sortMenuClick = (e)->
    console.log "sort change"
    console.log e.detail.item
    sortOptions = e.detail.items
    dispatch "sort", e.detail.item


  filterMenuClick = (e)->
    console.log "filter change"
    console.log e.detail
    filterOptions = e.detail
    dispatch "filter", e.detail



  checkSort = (list)->
    selectedItem = list.find (i)->
      return (i.selected == true)
    return selectedItem.ascending


  checkFilter = (list)->
    # if all false, not filtering
    return !list.every({selected: false})

  `$: currently_ascending = checkSort(sortOptions)`
  `$: currently_filtering = checkFilter(filterOptions)`
  `$: filteredList = filterFn(list,searchValue)`


</script>


  <div layout="row" name="header">
    <div flex><SearchInput bind:value={searchValue}/></div>
    {#if sortable}
    <div flex="initial">
      <button type="button" icon bind:this={sortMenuButtonRef} on:click={sortMenu}>
        {#if currently_ascending}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M4 12l1.41 1.41L11 7.83V20h2V7.83l5.58 5.59L20 12l-8-8-8 8z"/></svg>
        {:else}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M20 12l-1.41-1.41L13 16.17V4h-2v12.17l-5.58-5.59L4 12l8 8 8-8z"/></svg>
        {/if}
      </button>
      <SortMenu bind:showMenu={showSortMenu} items={sortOptions} on:click={sortMenuClick} bind:trigger={sortMenuButtonRef} align="right"/>
    </div>
    {/if}
    {#if filterable}
    <div flex="initial">
      <button type="button" icon bind:this={filterMenuButtonRef} on:click={filterMenu}>
        {#if currently_filtering}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 512 512"><polygon points="0 48 192 288 192 416 320 464 320 288 512 48 0 48"/></svg>
        {:else}
          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 512 512"><title>ionicons-v5-n</title><path d="M35.4,87.12,204.05,283.56A16.07,16.07,0,0,1,208,294V413.32a7.93,7.93,0,0,0,5.39,7.59l80.15,26.67A7.94,7.94,0,0,0,304,440V294A16.07,16.07,0,0,1,308,283.56L476.6,87.12A14,14,0,0,0,466,64H46.05A14,14,0,0,0,35.4,87.12Z" style="fill:none;stroke:#000;stroke-linecap:round;stroke-linejoin:round;stroke-width:32px"/></svg>
        {/if}
      </button>
      <FilterMenu bind:showMenu={showFilterMenu} items={filterOptions} on:click={filterMenuClick} bind:trigger={filterMenuButtonRef} align="right"/>
    </div>
    {/if}
  </div>
  <div id="content-{id}">
    {#each (filteredList) as item, index}
      <slot {item} {index}/>
    {/each}
    <slot name="bottom-content"/>
    {#if filteredList.length == 0}
    <slot name="blank-slate"/>
    {/if}
  </div>
