<script lang="coffeescript">

  import Section from "../../_Common/Sections/_Section.svelte"


  import FilteredList from '../../../components/Lists/FilteredList.svelte'

  export project = undefined
  export open = true

  filterFn = (listToFilter, searchString)->

    if searchString?
      searchString = searchString.toLowerCase()

      return listToFilter.filter (item)->
        return (item.name || "").toLowerCase().includes(searchString) || (item.description || "").toLowerCase().includes(searchString)

    else
      return listToFilter
  list = []


  `$: list = project.work_units || []`

</script>



