<script lang="coffeescript">


  # Lib

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()
  import Clip from "../../../helpers/clip.js"
  import Toast from "../../../components/Toast/Toast.svelte"

  # Components

  import Indent from "../Indent.svelte" # this is a bloody workaround - attribute was not updating on element
  import DraggableItem from "../../_Common/Lists/Nestable/DraggableIndentableItem.svelte"
  import DroppableDivider from "../../_Common/Lists/Nestable/DroppableDivider.svelte"
  import QuickEntry from "../../_Common/QuickEntry/Clip.svelte"
  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'


  import Section from "./_Section.svelte"
  import Menu from '../Menus/Clip.svelte'

  # Props

  export item = undefined

  export collection = "clips"
  export listId = uuid()
  export sameListOnly = true

  subDocument = new Clip

  dispatch = createEventDispatcher()



  message = ""
  showToast = false


  style = ":hover {font-weight: bold}"

  copy = (e)->
    console.log "COPY CLICK"
  copy = (val)->
    console.log "copy clicked"
    dummy = document.createElement("input")
    document.body.appendChild(dummy)
    dummy.setAttribute("id", "clip-#{val.id}")
    document.getElementById("clip-#{val.id}").value=val.content
    dummy.select()
    document.execCommand("copy")
    document.body.removeChild(dummy)
    # toast it!
    message = "Copied value for item named <b>#{val.name.truncateOnWord(255)}</b> to clipboard"
    showToast = true


  toggleVisibility = (val)->
    console.log val
    thisItem = item.clips.find {id: val.id}
    thisItem.is_visible = !thisItem.is_visible
    item[collection] = item[collection]
    dispatch "update"


  showDeleteConfirmation = false

  showDeleteModal = ()->
    showDeleteConfirmation = true

  deleteConfirmationCancel = ()->
    showDeleteConfirmation = false

  destroy = (e)->
    console.log "ClipList.destroy"
    console.log e.detail
    item[collection].remove {id: e.detail.id}
    item[collection] = item[collection]
    dispatch "update"
    showDeleteConfirmation = false


  updateName = (e)->
    console.log e.detail
    change =
      id: item.id
      name: e.detail
    dispatch "update", change

  updateAttribute = (e)->

    change = e.detail
    changedTask = item[collection].find { id: change.id }
    if changedTask?
      Object.merge(changedTask, change)
      item[collection] = item[collection]
      dispatch "update", change
    else
      console.log "unchanged"


  update = ()->
    dispatch "update"

  add = (event)->
    console.log "add"
    console.log event.detail
    newRecord = subDocument.stub(event.detail)
    newRecord.ordinal = (item[collection] || []).length
    item[collection] = item[collection].add newRecord
    dispatch "update"




  getDragGroup = (draggedItem)->
    dragGroup = []
    indexForDragged = item[collection].findIndex (o)->
      return o.id == draggedItem.id
    dragGroup.append draggedItem
    endLoop = false
    item[collection].forEach (i, index)->
      if index > indexForDragged # do not include the dragged item
        if item.indent <= draggedItem.indent
          endLoop = true
        unless endLoop
          dragGroup.append i
    return dragGroup

  shiftOrdinals = (dropIndex, length)->

    item[collection].forEach (i, index)->
      if index > dropIndex
        item[collection][index].ordinal = i.ordinal + length



  insertDraggedItemsAfterTarget = (dragGroup, dropTarget)->
    console.log "insertDraggedItemsAfterTarget"
    console.log dragGroup
    console.log dropTarget
    indentBase = dropTarget.indent + 1
    ordinalBase = dropTarget.ordinal + 1
    console.log "ordinal base"
    console.log ordinalBase
    console.log "insert dragged after target"

    minimumIndent = dragGroup.min (i)->
      return i.indent

    minimumIndent = minimumIndent.indent

    dragGroup.forEach (i, index)->
      dragGroup[index].indent = dragGroup[index].indent - minimumIndent

    dragGroup.forEach (i, index)->
      itemToChange = item[collection].findIndex { id: i.id }
      item[collection][itemToChange].ordinal = ordinalBase + index
      item[collection][itemToChange].indent = (indentBase + i.indent)







  dropEvent = (e)->
    refresh = false
    # set the drag group
    console.log "dropevent"
    console.log e.detail
    dragGroup = getDragGroup(e.detail.dragItem)
    if e.detail.divider?
      if (e.detail.dropIndex == -1) || (e.detail.dropIndex == (item[collection].length - 1)) # first or last item, move to root
        shiftOrdinals e.detail.dropIndex, dragGroup.length
        insertDraggedItemsAfterTarget dragGroup, {indent: -1, ordinal: e.detail.dropIndex}

        refresh = true
      else if (e.detail.dragIndex || 0) != e.detail.dropIndex

        matchingItem = item[collection][e.detail.dropIndex]
        if dragGroup.find({id: matchingItem.id})?
          console.log "dropped on a child's divider"

        else
          console.log "dropped on a non-child's divider"
          console.log e.detail.dragItem
          # grab the next item
          # rule
          # match the indent of the item below the divider
          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem

          refresh = true

      else
        console.log "dropped on same item's divider"
        refresh = false
    else

      # todo: what if dropped on a child?

      unless e.detail.dragItem.id == e.detail.dropItem.id # otherwise do nothing

        unless dragGroup.find({id: e.detail.dropItem.id})

          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem
          refresh = true

        else
          console.log "tried to drop on child, nope"



    if refresh
      item[collection].sortBy "ordinal"
      item[collection].forEach (item, index)->
        item.ordinal = index
        dispatch "update"


  rowEl = undefined

  sort = (list)->
    if item[collection]?
      return item[collection].sortBy "ordinal"
    else
      []

  getWidth = (el)->
    if el?
      el.clientWidth
    else
      0
  `$: list = sort(item[collection])`
  `$: show = showToast`





</script>
<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
  .fill
    fill: var(--gray)
    &:hover
      fill: var(--blue)
</style>

<Section name={collection.titleize()} {open}>

  {#if list && list.length}
    <div>
      <DroppableDivider index={-1} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as clipItem, index}
        <DraggableItem item={clipItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>

          <div layout="row" bind:this={rowEl} type="list-item">
            <Indent level={clipItem.indent.clamp(0,5)}/>
            <div flex="initial"  style="padding-right: 8px">
              <button type="button" icon on:click|preventDefault|stopPropagation={copy(clipItem)}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M19,21H8V7H19M19,5H8A2,2 0 0,0 6,7V21A2,2 0 0,0 8,23H19A2,2 0 0,0 21,21V7A2,2 0 0,0 19,5M16,1H4A2,2 0 0,0 2,3V17H4V3H16V1Z" class="fill"/></svg>
              </button>
            </div>
            <div flex ellipsis main>
              <InlineInput bind:value={clipItem.name} on:update={updateName} {style} let:label>
                <label>{ label || "No name"}</label>
              </InlineInput>



                <InlineInput bind:value={clipItem.content} on:update={updateName} {style} let:label>
                  {#if clipItem.is_visible}
                    { label || "No content"}
                  {:else}
                    <span type="hidden">Value is Hidden</span>
                  {/if}
                </InlineInput>




            </div>

          </div>

          <div slot="menu">
            <Menu item={clipItem} on:update={updateAttribute} on:destroy={destroy}/>
          </div>
        </DraggableItem>
        <DroppableDivider item={clipItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}
  <QuickEntry on:add={add}/>

  <Toast bind:show={showToast} {message}/>

</Section>
