<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import Indent from "../Indent.svelte"
  import DraggableItem from "../Lists/Nestable/DraggableIndentableItem.svelte"
  import DroppableDivider from "../Lists/Nestable/DroppableDivider.svelte"

  import QuickEntry from "../QuickEntry/GenericItem.svelte"

  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'
  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'



  import Menu from '../Menus/SubDocument.svelte'

  import ProgressPopover from '../Popovers/Progress.svelte'
  import DueDatePopover from '../Popovers/DueDate.svelte'
  import AssignmentPopover from '../Popovers/Assignment.svelte'
  import TimePopover from '../Popovers/Time.svelte'
  import ChecklistPopover from '../Popovers/Checklist.svelte'
  import CommentPopover from '../Popovers/Comment.svelte'


  import Section from "./_Section.svelte"

  export assignments = []


  # optional
  export showProgress = true
  export showAssignment = true
  export showChecklist = false

  export collection = "tasks"

  import Task from "../../../helpers/task.js"
  import Assignment from "../../../helpers/assignment.js"
  import Document from "../../../helpers/document.js"



  export item = undefined

  subDocument = undefined

  mounted = false
  onMount ()->
    switch collection
      when "tasks"
        subDocument = new Task
      when "documents"
        subDocument = new Document
      when "assignments"
        subDocument = new Assignment
      # }
    item[collection].forEach (subitem)->
      subitem = subDocument.upgradeModel(subitem)
    mounted = true



  showComments = (item)->
    if item.comments?
      return (item.comments.length > 0)
    else
      return false




  goToItem = (childItem)->
    location.href="#/projects/#{item.id}/#{collection}/#{childItem.id}"

  style = ":hover {font-weight: bold}"


  updateName = (e)->
    console.log e.detail
    change =
      id: item.id
      name: e.detail
    dispatch "update", change

  updateAttribute = (e)->
    console.log "ListItme.svelte updateAttribute"
    console.log e.detail
    change = e.detail
    # change["id"] = item.id
    changedTask = item[collection].find { id: change.id }
    Object.merge(changedTask, change)
    item[collection] = item[collection] # refresh array
    console.log change
    dispatch "update", change


  add = (event)->
    console.log "SubDocumentList.add"

    newRecord = subDocument.stub(event.detail)
    newRecord.ordinal = (item[collection] || []).length
    console.log newRecord
    item[collection] = item[collection].add newRecord
    dispatch "update"


  destroy = (e)->
    item[collection].remove {id: e.detail.id}
    item[collection] = item[collection]
    dispatch "update"
    showDeleteConfirmation = false





  hasAssignments = ()->
    output = false
    if item.assignments?
       output = (item.assignments.length > 0)
    return output

  `$: showAssignment = hasAssignments(item)`

  update = ()->
    dispatch "update"



  sort = (list)->
    if item[collection]?
      return item[collection].sortBy "ordinal"
    else
      []



  export listId = uuid()
  export sameListOnly = true




  getDragGroup = (draggedItem)->
    dragGroup = []
    indexForDragged = item[collection].findIndex (o)->
      return o.id == draggedItem.id
    dragGroup.append draggedItem
    endLoop = false
    item[collection].forEach (i, index)->
      if index > indexForDragged # do not include the dragged item
        if item.indent <= draggedItem.indent
          endLoop = true
        unless endLoop
          dragGroup.append i
    return dragGroup

  shiftOrdinals = (dropIndex, length)->

    item[collection].forEach (i, index)->
      if index > dropIndex
        item[collection][index].ordinal = i.ordinal + length



  insertDraggedItemsAfterTarget = (dragGroup, dropTarget)->
    console.log "insertDraggedItemsAfterTarget"
    console.log dragGroup
    console.log dropTarget
    indentBase = dropTarget.indent + 1
    ordinalBase = dropTarget.ordinal + 1
    console.log "ordinal base"
    console.log ordinalBase
    console.log "insert dragged after target"

    minimumIndent = dragGroup.min (i)->
      return i.indent

    minimumIndent = minimumIndent.indent

    dragGroup.forEach (i, index)->
      dragGroup[index].indent = dragGroup[index].indent - minimumIndent

    dragGroup.forEach (i, index)->
      itemToChange = item[collection].findIndex { id: i.id }
      item[collection][itemToChange].ordinal = ordinalBase + index
      item[collection][itemToChange].indent = (indentBase + i.indent)







  dropEvent = (e)->
    console.log "subdocumentlist drop"
    refresh = false
    # set the drag group
    console.log "dropevent"
    console.log e.detail
    dragGroup = getDragGroup(e.detail.dragItem)
    if e.detail.divider?
      if (e.detail.dropIndex == -1) || (e.detail.dropIndex == (item[collection].length - 1)) # first or last item, move to root
        shiftOrdinals e.detail.dropIndex, dragGroup.length
        insertDraggedItemsAfterTarget dragGroup, {indent: -1, ordinal: e.detail.dropIndex}

        refresh = true
      else if (e.detail.dragIndex || 0) != e.detail.dropIndex

        matchingItem = item[collection][e.detail.dropIndex]
        if dragGroup.find({id: matchingItem.id})?
          console.log "dropped on a child's divider"

        else
          console.log "dropped on a non-child's divider"
          console.log e.detail.dragItem
          # grab the next item
          # rule
          # match the indent of the item below the divider
          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem

          refresh = true

      else
        console.log "dropped on same item's divider"
        refresh = false
    else

      # todo: what if dropped on a child?

      unless e.detail.dragItem.id == e.detail.dropItem.id # otherwise do nothing

        unless dragGroup.find({id: e.detail.dropItem.id})

          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem
          refresh = true

        else
          console.log "tried to drop on child, nope"



    if refresh
      item[collection].sortBy "ordinal"
      item[collection].forEach (item, index)->
        item.ordinal = index
        dispatch "update"



  `$: list = sort(item[collection])`
</script>
<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
    [present]
      display: none
    [present="true"]
      display: flex
    [main]

    &:hover
      [present]
        display: flex

</style>

<Section name={collection.titleize()} {open}>

  {#if mounted}
    <div>
      <DroppableDivider index={-1} {listId} {sameListOnly} on:drop={dropEvent} />
      {#each list as listItem, index}
        <DraggableItem item={listItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>
          <div layout="row" type="list-item">
            <Indent level={listItem.indent.clamp(0,5)}/>
            {#if showProgress}
              <div flex="initial" section="progress">
                <ProgressPopover item={listItem} bind:value={listItem.progress} on:update={updateAttribute} on:open on:close/>
              </div>
            {/if}
            <div flex ellipsis main>
              <InlineInput bind:value={listItem.name} on:update={updateName} on:clicked={goToItem(listItem)} {style} let:label>
                <span ellipsis><a href="#/projects/{item.id}/{collection}/{listItem.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
              </InlineInput>
            </div>
            <div flex="initial">
              <div layout="row">



                <!-- due date menu -->
                <div flex="initial" section="due-date" present={listItem.due_date != null}>
                  <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateAttribute} on:open on:close/>
                </div>
                <div flex="initial" section="time" present={false}>
                  <TimePopover item={listItem} value={listItem.due_date} on:update={updateAttribute} on:open on:close showTimeForm={true}/>
                </div>

                {#if showChecklist}
                  {#if (listItem.checklists || []).length > 0}
                  <div flex="initial">
                    <ChecklistPopover item={listItem} on:open on:close/>
                  </div>
                  {/if}
                {/if}
                {#if showComments(item)}
                  {#if (listItem.comments || []).length > 0}
                  <div flex="initial">
                    <CommentPopover item={listItem} on:open on:close/>
                  </div>
                  {/if}
                {/if}
                <!-- assignment menu -->
                {#if (listItem.type != "assignment") && (item.assignments.length > 0)}
                  <div flex="initial" section="assignment" present={(listItem.assigned_to || []).length > 0}>
                    <AssignmentPopover project={item} bind:item={listItem} on:update={updateAttribute} on:open on:close/>
                  </div>
                {/if}

              </div>
            </div>

          </div>

          <div slot="menu">
            <Menu item={listItem} on:update={updateAttribute} on:destroy={destroy}/>


          </div>
        </DraggableItem>
        <DroppableDivider item={listItem} index={index} {listId} {sameListOnly} on:drop={dropEvent} />
      {/each}
    </div>
  {/if}

  <QuickEntry on:add={add} stubPlaceholder="Add {collection.singularize().titleize()}" />

</Section>
