<script lang="coffeescript">


  import { createEventDispatcher, onMount, afterUpdate } from 'svelte'
  import { slide } from 'svelte/transition'


  dispatch = createEventDispatcher()

  export disabled = false
  export id = uuid()
  export items = []
  export width = '200px'

  export selectedIndex = -1
  export size = undefined


  export value = ""
  export blankSlate = ""

  export labelKey = "label"
  export idKey = "id"


  export showMenu = false

  export align = "left"

  # trigger is required
  export trigger = undefined

  # if a trigger is defined, it maps the width to the parent
  # and is placed just below it, used for combobox and custom select
  export matchTrigger = false


  style = undefined

  menuRef = undefined

  hasfocus = false

  highlightedIndex = -1

  changeIndex = (direction) ->
    index = highlightedIndex + direction;

    if (index < 0)
      index = items.length - 1
    else if (index >= items.length)
      index = 0
    highlightedIndex = index



  menuId = uuid()

  onMount ()->
    console.log "parent, mounted"



  checkTrigger = (i)->
    if i?
      if matchTrigger
        style = "width: #{width};top: #{i.clientHeight + 4}px; width: #{i.clientWidth + 2}px"
      else

        if align == "left"
          style = "width: #{width};top: #{i.offsetParent.clientHeight + 4}px;"
        else
          triggerContainerWidth = i.offsetParent.clientWidth
          triggerCoords = i.getBoundingClientRect()
          right = triggerContainerWidth  - (triggerCoords.width)
          style = "width: #{width};top: #{i.offsetParent.clientHeight + 4}px; right: #{right}px"
    console.log "trigger"
    console.log i
    console.log "style"
    console.log style

  `$: checkTrigger(trigger)`

  menuItemClick = (item, index)->
    changedItem = null
    items.forEach (i)->
      if i.id == item.id
        if i.selected
          i.ascending = !i.ascending
        else
          i.selected = true
        changedItem = i
      else
        i.selected = false

    `showMenu = false`
    dispatch "click", {
      item: changedItem
      items: items
    }


  overlayClick = ()->
    `showMenu = false`


</script>


<style lang="sass">


  div[menu]
    position: absolute

    background: white
    display: inline-block
    border: 1px solid var(--menu-border)
    width: auto
    text-align: left
    z-index: var(--z-index-menu)
    &[align="left"]
      left: 0


  [menu-item]
    display: block
    position: relative
    width: 100%
    padding: var(--fine-spacing-s) var(--fine-spacing-m)
    &:hover
      background: var(--lighter-gray)

  button


</style>
<div type="overlay" clear open={showMenu} on:click={overlayClick}/>
{#if showMenu}
  <div bind:this={menuRef} menu id={menuId} transition:slide="{{duration: 75}}" {style} {align}>
    {#each items as item, i (item.id || i)}
      <button menu-item id={item[idKey]} active={selectedIndex === i} highlighted={highlightedIndex === i || selectedIndex === i} on:click={menuItemClick(item,i)}>
        <div layout="row">
          <div flex="initial">
            {#if item.selected}
              {#if item.ascending}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M4 12l1.41 1.41L11 7.83V20h2V7.83l5.58 5.59L20 12l-8-8-8 8z"/></svg>
              {:else}
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M20 12l-1.41-1.41L13 16.17V4h-2v12.17l-5.58-5.59L4 12l8 8 8-8z"/></svg>
              {/if}
            {:else}
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"></svg>
            {/if}
          </div>
          <div flex>{item[labelKey]}</div>
        </div>
      </button>
    {/each}
    {#if items.length == 0}
      <button menu-item>{@html blankSlate}</button>
    {/if}
  </div>
{/if}
