<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {push, pop, replace, location} from 'svelte-spa-router'


  import Indent from "../Indent.svelte"

  import DraggableItem from "../Lists/Nestable/DraggableIndentableItem.svelte"
  import DroppableDivider from "../Lists/Nestable/DroppableDivider.svelte"

  import QuickEntry from "../QuickEntry/GenericItem.svelte"

  import Menu from "../Menus/Project.svelte"

  import InlineInput from '../../../components/Form/InlineInput/InlineInput.svelte'
  import Tooltip from '../../../components/Overlay/Tooltip/Tooltip.svelte'

  import PageHeader from '../PageHeader.svelte'
  import ProgressPopover from '../Popovers/Progress.svelte'
  import DueDatePopover from '../Popovers/DueDate.svelte'
  import EstimatePopover from '../Popovers/Time.svelte'




  import Section from "./_Section.svelte"

  export assignments = []


  # optional

  export collection = "tasks"

  import Project from "../../../helpers/project.js"
  import Assignment from "../../../helpers/assignment.js"


  export list = undefined
  export item = undefined
  itemUrl = "#/projects/"

  subDocument = new Project



  showComments = (item)->
    if item.comments?
      return (item.comments.length > 0)
    else
      return false




  goToItem = (item)->
    push "/projects/#{item.id}"




  updateProject = (p)->

    list = list # refresh array
    # preclean the ordinals
    list.forEach (item, index)->
      item.ordinal = index
      if isNaN(item.indent) || !item.indent?
        item.indent = 0
    console.log p
    dispatch "update", p


  updateAttribute = (e)->
    console.log "ListItme.svelte updateAttribute"
    console.log e.detail
    change = e.detail
    # change["id"] = item.id
    changedItem = list.find { id: change.id }
    Object.merge(changedItem, change)


    list = list # refresh array
    # preclean the ordinals
    list.forEach (item, index)->
      item.ordinal = index
      if isNaN(item.indent) || !item.indent?
        item.indent = 0
    console.log change
    dispatch "update", change


  add = (e)->
    dispatch "add", e.detail



  hasAssignments = ()->
    output = false
    if item.assignments?
       output = (item.assignments.length > 0)
    return output



  update = ()->
    dispatch "update"


  destroy = (e)->
    dispatch "destroy", e.detail
  sort = (list)->
    if list?
      return list.sortBy "ordinal"
    else
      []



  export listId = uuid()
  export sameListOnly = true




  getDragGroup = (draggedItem)->
    dragGroup = []
    indexForDragged = list.findIndex (o)->
      return o.id == draggedItem.id
    dragGroup.append draggedItem
    endLoop = false
    list.forEach (item, index)->
      if index > indexForDragged # do not include the dragged item
        if item.indent <= draggedItem.indent
          endLoop = true
        unless endLoop
          dragGroup.append item
    return dragGroup

  shiftOrdinals = (dropIndex, length)->

    list.forEach (item, index)->
      if index > dropIndex
        list[index].ordinal = item.ordinal + length



  insertDraggedItemsAfterTarget = (dragGroup, dropTarget)->
    indentBase = dropTarget.indent + 1
    ordinalBase = dropTarget.ordinal + 1
    console.log "ordinal base"
    console.log ordinalBase
    console.log "insert dragged after target"

    minimumIndent = dragGroup.min (i)->
      return i.indent

    minimumIndent = minimumIndent.indent

    dragGroup.forEach (i, index)->
      dragGroup[index].indent = dragGroup[index].indent - minimumIndent

    dragGroup.forEach (item, index)->
      itemToChange = list.findIndex { id: item.id }
      list[itemToChange].ordinal = ordinalBase + index
      list[itemToChange].indent = (indentBase + item.indent)


  dropEvent = (e)->
    refresh = false
    # set the drag group
    dragGroup = getDragGroup(e.detail.dragItem)
    if e.detail.divider?
      if (e.detail.dropIndex == -1) || (e.detail.dropIndex == (list.length - 1)) # first or last item, move to root
        shiftOrdinals e.detail.dropIndex, dragGroup.length
        insertDraggedItemsAfterTarget dragGroup, {indent: -1, ordinal: e.detail.dropIndex}

        refresh = true
      else if (e.detail.dragIndex || 0) != e.detail.dropIndex

        matchingItem = list[e.detail.dropIndex]
        if dragGroup.find({id: matchingItem.id})?
          console.log "dropped on a child's divider"
        else
          console.log "dropped on a non-child's divider"
          # grab the next item
          # rule
          # match the indent of the item below the divider
          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem

          refresh = true

      else
        console.log "dropped on same item's divider"
        refresh = false
    else

      # todo: what if dropped on a child?

      unless e.detail.dragItem.id == e.detail.dropItem.id # otherwise do nothing

        unless dragGroup.find({id: e.detail.dropItem.id})

          # shift the ordinals after the drop target
          shiftOrdinals e.detail.dropIndex, dragGroup.length

          # move the items
          insertDraggedItemsAfterTarget dragGroup, e.detail.dropItem
          refresh = true

        else
          console.log "tried to drop on child, nope"



    if refresh
      list.sortBy "ordinal"
      list.forEach (item, index)->
        item.ordinal = index
        dispatch "update", item

  style = ":hover {font-weight: bold}"
  add = (event)->
    newRecord = subDocument.stub(event.detail)
    scb = (inserted)->
      console.log inserted
      push "/projects/#{inserted.id}"
    newRecord.ordinal = (list || []).length
    subDocument.insert newRecord, scb, null, null




  `$: sortedlist = sort(list)`
</script>
<style lang="sass">
  [type="list-item"]
    width: 100%
    max-width: 100%
    [present]
      display: none
    [present="true"]
      display: flex
    [main]

    &:hover
      [present]
        display: flex

  header
    h1
      font-size: 36px
      color: var(--darkest-blue)
  section
    margin: 0px 56px

</style>

  <PageHeader>Collections</PageHeader>
  <section>
    {#if list && list.length}
      <div>
        <DroppableDivider index={-1} {listId} {sameListOnly} on:drop={dropEvent} />
        {#each sortedlist as item, index}
          <DraggableItem item={item} index={index} {listId} {sameListOnly} on:drop={dropEvent} on:update={updateAttribute}>
            <div layout="row" type="list-item">
              <Indent level={item.indent.clamp(0,5)}/>

              <div flex="initial" section="progress">
                <ProgressPopover {item} bind:value={item.progress} on:update={updateAttribute} on:open on:close/>
              </div>

              <div flex ellipsis>
                <InlineInput bind:value={item.name} on:update={updateProject(item)} on:clicked={goToItem(item)} {style} let:label>
                  <span ellipsis><a href="{itemUrl}{item.id}" on:click|preventDefault><em>{label || "No Name"}</em></a></span>
                </InlineInput>
              </div>
              <div flex="initial">
                <div layout="row">



                  <!-- due date menu -->
                  <div flex="initial" present={item.due_date != null}>
                    <DueDatePopover {item} value={item.due_date} on:update={updateAttribute} on:open on:close/>
                  </div>
                  <div flex="initial" present={(item.estimate || 0) > 0} style="padding-left: 8px">
                    <EstimatePopover {item}  on:update={updateAttribute} on:open on:close/>
                  </div>



                </div>
              </div>

            </div>

            <div slot="menu">
              <Menu {item} on:update={updateAttribute} on:destroy={destroy}/>

            </div>
          </DraggableItem>
          <DroppableDivider {item} index={index} {listId} {sameListOnly} on:drop={dropEvent} />
        {/each}
      </div>
    {/if}
    <QuickEntry on:add={add} stubPlaceholder="Add Collection" />
  </section>
